
    import Vue from "vue"
	import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

	export default {
	  components: {
	    Group,
	    Cell,
		XInput,
		Datetime,
		XTextarea,
		XButton,
	    PopupPicker
	  },
	  data () {
    	return {
    		customValue:[],//
    		customLists: [['']],//
    		addressValue:[],//
    		addressLists: [['']],//
    		sendValue:[],//
    		sendLists: [['']],//
    		accountValue:[],//
    		accountLists: [['']],//
    		ticketValue:[],//
    		ticketLists: [['']],//
			shopValue:[],//
    		shopLists:[['']],//
    		shopNumValue:'',//
    		shopPriceValue:'',//
    		taxValue:[],//
    		taxLists:[['']],//
    		transportValue:'',//
    		loadedValue:'',//
    		orderTime:'',//
    		orderRemark:'',//
			orderInfo: {}, //
			orderDetailJson: {}, //
			sale_unit_id_str:'KG',
			unitLists:[[]],//
			tagData: {
				    "order_no":"",
					"order_type_stand":"",
					"sap_order_no":"SAP",
					"status_string":"",
					"sale_unit":"",
					"saler":"",
					"remark":"",
					"deliver_status_string":"",
					"currency_code":"",
					"package_qty":"",
					"dock_free":"",
					"number":"PQ",
					"price_difference":"",
					"order_type_free":"",
					"customer_product_code":"",
					"cust_product_code":"",
					"price":"",
					"ship_model":"",
					"sale_office":"",
					"shipping_address":"",
					"settle_way":"",
					"material_name":"",
					"order_type":"",
					"customer_reach_date":"",
					"deliver_time":"",
					"transport_free":"",
					"is_contract":"",
					"create_time":"",
					"free_reason":"",
					"detail_remark":"",
					"tax":"",
					"transport":"",
					"packing_way":"",
					"ticket_way":"",
					"is_dock_tax":"",
					"money":"",
					"sysn_time":"",
					"is_promotion":"",
					"material_id":"",
					"customer_reach_dateStr":"",
					"factory_string":"",
					"customer_name":""
			}
    	}
     },
	 created: function() {
		 let self = this;
		 let jumpData = self.$route.query.data;
		 if(jumpData){
			 jumpData = JSON.parse(jumpData);
			 self.jumpInitData(jumpData);

		 }
		 
		 self.initTag();//
		 let order_type = self.$route.query.type;//
		 order_type = ''; //todo  12
		 self.orderInfo.order_type = order_type;
		//  self.initSearchCustomer();
		 self.initDict('customer',self.customLists);//
		 self.initDict('transport_way',self.sendLists);//
		 self.initDict('settle_way',self.accountLists);//
		 self.initDict('ticket_way',self.ticketLists);//
		 self.initDict('tax',self.taxLists);//
		 self.initDict('product',self.shopLists);//
		 self.initDict('shipping_address',self.addressLists);//
		 self.initDict('sale_unit',self.unitLists);//
	 },
     methods:{
		 /**
		  * 
		  */
		 submitData:function(){
			 let self = this;
			 self.initFormData();
			 let orderInfo = self.orderInfo;
			 let orderDetailJson = self.orderDetailJson;
			 let orderInfos = [];
			 orderInfos.push(orderInfo);
			 let orderDetailJsons = [];
			 orderDetailJsons.push(orderDetailJson);

			 if(!self.submitCheck()){
				 self.$vux.toast.show({
					type: "warn",
					text: ''
				});
				return;
			 }

			 self.$http.get('/jhhxPlatform/order/createOrder.do',{
				 params:{
					 token:g.token,
					 orderInfo: JSON.stringify(orderInfos),
					 orderDetailJson: JSON.stringify(orderDetailJsons)
				 }
			 }).then(function(res){
				 if(res.status == 200 && res.statusText == 'OK'){
					 console.log(res);
				 }
			 });
		 },
		 /**
		  * 
		  */
		 initSearchCustomer: function(){
			 let self = this;
			 self.$http.get('/jhhxPlatform/customer/findByPage.do',{
				 params:{
					 token:g.token,
					 page:'1',
					 rows:'100'
				 }
			 }).then(function(res){
				 if(res.status == 200 && res.statusText == 'OK'){
					 let customerData = res.data.rows;
					 self.customLists[0].splice(0,self.customLists[0].length);//
					 for(let i=0; i<customerData.length;i++){
						 let child = {};
						 child.name = customerData[i].customer_name;
						 child.value = customerData[i].customer_name;
						 child.data = customerData[i];
						 self.customLists[0].push(child);
					 }
				 }
			 });
		 },
		 /**
		  * 
		  */
		 initDict: function(type,backContent){
			 let self = this;
			 self.$http.get('/jhhxPlatform/dic/getDicByType.do',{
				 params:{
					 token:g.token,
					 type:type
				 }
			 }).then(function(res){
				 if(res.status == 200 && res.statusText == 'OK'){
					 if(res.data.length != 0){
						backContent[0].splice(0,backContent[0].length);//
						for(let i = 0 ; i<res.data.length ; i++){
							let child = {};
							if(type == 'settle_way'){
								child.name = res.data[i].dicDesc;
								child.value = res.data[i].dicDesc;
								child.data = res.data[i];
							}else{
								child.name = res.data[i].dicTxt;
								child.value = res.data[i].dicTxt;
								child.data = res.data[i];
							}
							
							backContent[0].push(child);
						}
					 }
				 }
			 });
		 },
		 /**
		  * 
		  */
		  submitCheck: function(){
			  let self = this;
			  let datas = [];
			 datas.push(self.customValue[0]);
			 datas.push(self.addressValue[0]);
			 datas.push(self.sendValue[0]);
			 datas.push(self.accountValue[0]);
			 datas.push(self.ticketValue[0]);
			 datas.push(self.shopValue[0]);
			 datas.push(self.taxValue[0]);
			 datas.push(self.transportValue);
			 datas.push(self.shopNumValue);
			 for(let i = 0 ;i<datas.length ; i++){
				 if(!datas[i]){
					 return false;
				 }
			 }
			 return true;
		  },
		 /**
		  * 
		  */
		 handleChange: function(str){
			 let self = this;
			 let orderInfo = self.orderInfo;
			 let orderDetailJson = self.orderDetailJson;
			 let data;
			 switch(str){
				 case "customer": //
				 	data = self.customLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(data[0].length != 0 && self.customValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.customer_id = data[i].data.dicId + '';
							 let strs = self.subStr(data[i].name);
							 if(strs.length==2){
								 self.orderInfo.customer_code = strs[0];
								 self.orderInfo.customer_name = strs[1];
							 }else{
								 self.orderInfo.customer_code = data[i].data.dicId + '';
							 	 self.orderInfo.customer_name = data[i].data.dicId + '';
							 }
							 return;
						 }
					}
					 break;
				 case "address": // 
					data = self.addressLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.addressValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.shipping_address = data[i].name;
							 self.orderInfo.ship_to = data[i].data.dicId + '';
							 return;
						 }
					}
				 	 break;
				 case "send"://
					data = self.sendLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.sendValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.ship_model_id = data[i].data.dicId + '';
							 return;
						 }
					}
					 break;
				 case "ticket": // 
				 	data = self.ticketLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.ticketValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.ticket_way_id = data[i].data.dicId + '';
							 return;
						 }
					}
				 	 break;
				 case "shop": //
					data = self.shopLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.shopValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderDetailJson.product_id = data[i].data.dicId + '';
							 self.orderDetailJson.material_id = data[i].data.material_id;
							 self.orderDetailJson.material_name = data[i].data.material_name;
							 self.orderDetailJson.customer_product_id = data[i].data.material_id;
							 self.orderDetailJson.customer_product_code = data[i].data.material_name;
							 let units = self.unitLists[0];
							 for(let j = 0; j < units.length; j++){
								 if(data[i].data.sale_unit == units[j].name){
									 self.orderDetailJson.sale_unit_id = units[j].data.dicId + '';
									 self.sale_unit_id_str = units[j].data.dicTxt; //
								 }
							 }
							 return;
						 }
					}
				 	 break;
				 case "tax": // 
				 	data = self.taxLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.taxValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderDetailJson.is_dock_tax = data[i].name;
							 self.orderDetailJson.tax_id = data[i].data.dicId + '';
							 return;
						 }
					}
				 	 break;
				case "account": //
				 	data = self.accountLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.accountValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.settle_way_id = data[i].data.dicId + '';
							 return;
						 }
					}
				 	 break;
				 default:
				 	break;
			 }
		 },
		 /**
		  * 
		  */
		 subStr: function(str){
			 let index = str.indexOf(')');
			 let strs = [];
			 if(index == -1){
				 return strs.push(str);
			 }
			 strs.push(str.substring(0,index+1));
			 strs.push(str.substring(index+1,str.length));
			 return strs;
		 },
		 /**
		  * 
		  */
		jumpInitData: function(data){
			let self = this;
			//  
			self.customValue[0] = data.customer_code + data.customer_name; //
			self.addressValue[0] = data.shipping_address; //
			self.sendValue[0] = data.ship_model; //
			self.accountValue[0] = data.settle_way; //
			self.ticketValue[0] = data.ticket_way; //
			self.shopValue[0] = data.material_name; //
			self.shopNumValue = data.number; //
			self.sale_unit_id_str = data.sale_unit; //
			self.shopPriceValue = data.price; //
			self.taxValue[0] = data.tax; //
			self.transportValue = data.transport_free; //
			self.loadedValue = data.dock_free; //
			self.orderTime = data.customer_reach_dateStr; //
			self.orderRemark = data.detail_remark; //

			//
			let orderInfo = self.orderInfo;
			let orderDetailJson = self.orderDetailJson;

			 orderInfo.ship_model_id = data.ship_model_id; //Id *
			 orderInfo.settle_way_id = data.settle_way_id; //Id *
			 orderInfo.ticket_way_id = data.ticket_way_id; //Id *
			 orderInfo.customer_id = data.customer_id; //id *
			 orderInfo.customer_code = data.customer_code; //
			 orderInfo.customer_name = data.customer_name; // *
			 orderInfo.shipping_address = data.shipping_address; // *
			 orderInfo.ship_to = data.ship_to; // *
			 orderInfo.order_type = data.order_type; //* 

			
			 orderDetailJson.product_id = data.product_id;//Id *
			 orderDetailJson.material_id = data.material_id;// *
			 orderDetailJson.material_name = data.material_name;// *
			 orderDetailJson.tax_id = data.tax_id;//id
			 orderDetailJson.is_dock_tax = data.is_dock_tax;//
			 orderDetailJson.sale_unit_id = data.sale_unit_id;// *

		},
		/**
		 * 
		 */
		 initFormData: function(){
			 let self = this;
			 let orderInfo = self.orderInfo;
			 let orderDetailJson = self.orderDetailJson;

			 orderInfo.order_no = Date.parse(new Date()) +''; // *
			 orderInfo.saler_id = ''; //ID * 
			 orderInfo.billing_to = ''; //
			 orderInfo.status = '10'; //
			 orderInfo.currency_code = '1'; // * 1:2: [{"dicDesc":"CNY","dicTxt":"CNY","dicId":1},{"dicDesc":"USD","dicTxt":"USD","dicId":2}]
			 orderInfo.is_contract = '0'; // * //1  [{"dicDesc":"","dicTxt":"","dicId":0},{"dicDesc":"","dicTxt":"","dicId":1}]
			 orderInfo.order_language = 'cn'; // cn en 
			 orderInfo.remark = ''; //
			 //  orderInfo.ship_model_id = ''; //Id *
			//  orderInfo.settle_way_id = ''; //Id *
			//  orderInfo.ticket_way_id = ''; //Id *
			//  orderInfo.customer_id = self.customValue[0]; //id *
			//  orderInfo.customer_code = ''; //
			//  orderInfo.customer_name = ''; // *
			//  orderInfo.shipping_address = ''; // *
			//  orderInfo.ship_to = ''; // *
			//  orderInfo.order_type = '1'; //* 

			 orderDetailJson.customer_product_id = '1';//Id * 
			 orderDetailJson.customer_product_code = '';//
			 orderDetailJson.packing_way_id = '1';// *   [{"dicTxt":"(25KG)","dicId":1},{"dicTxt":"(10KG)","dicId":2},{"dicTxt":"(15KG)","dicId":3},{"dicTxt":"(50KG)","dicId":4}]
			 orderDetailJson.number = self.shopNumValue;//
			 orderDetailJson.price = self.shopPriceValue;// *
			 orderDetailJson.money = Number(self.shopPriceValue) * Number(self.shopNumValue);// *
			 orderDetailJson.transport_free = self.transportValue;//
			 orderDetailJson.dock_free = self.loadedValue;//
			 orderDetailJson.customer_reach_date = self.orderTime;// *
			 orderDetailJson.remark = self.orderRemark;//
			//  orderDetailJson.product_id = '';//Id *
			//  orderDetailJson.material_id = '';// *
			//  orderDetailJson.material_name = '';// *
			//  orderDetailJson.tax_id = '';//id
			//  orderDetailJson.is_dock_tax = '';//
			//  orderDetailJson.sale_unit_id = '';// *
		 },
		 /**
		  * 
		  */
		 initTag: function(){
			 let self = this;
			self.$http.get('/jhhxPlatform/order/getJhhxTitleLabel.do',{
				params:{
					token: g.token,
					code: '2,3' // code2,310
				}
			}).then(function(res){
				if(res.status == 200 && res.statusText == 'OK'){
					 self.tagData = res.data;
				}
			});
		 }

     }
}



